<template>
  <div class="certificate" v-loading.body="loading">
    <crumb></crumb>
    <div class="mainContainer">
      <div class="certificateContainer" v-if="isComplete">
        <div class="certificateBody">
          <div class="bgImg"></div>
          <!-- <img src="@/assets/img/school.png" class="logo"> -->
           <img src="@/assets/img/xikedalogo.png" class="logo">
          <div class="title">
            <h1>学生能力微文凭</h1>
            <h3>Microdiploma in Student Ability</h3>
          </div>
          <div class="content">
            学生<span>{{info.name}}</span>，性别<span>{{info.gender === 0 ? '男' : '女'}}</span>，生日<span>{{info.birthday}}</span>，于<span>{{info.startGrade}}</span>至<span>{{info.endGrade}}</span>在我校<span>{{info.specialName}}</span>专业完成<span>{{info.studyLen + '制'}}</span>本科学习，达成工程教育认证的毕业要求！
            <!-- <img src="@/assets/img/photo.png" alt=""> -->
            <img src="@/assets/img/photo1.png" alt="">
          </div>
          <div class="tableBox">
            <table v-for="(item, index) in info.claims" :key="index">
              <thead>
                <tr>
                  <th width="60%">{{item.claimname}}</th>
                  <th></th>
                  <th>结果</th>
                  <th>（期望值</th>
                  <th>达成值）</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(point, i) in item.points" :key="i">
                  <td><div>{{point.pointcode.split('-').join('.')}}</div><span>{{point.pointname}}</span></td>
                  <td style="vertical-align:top; color: #ddd">-----------</td>
                  <td style="vertical-align:top">{{point.target > point.analysis ? '未达成' : '达成'}}</td>
                  <td style="vertical-align:top">{{'（' + point.target}}</td>
                  <td style="vertical-align:top">{{point.analysis + '）'}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="footer">
            <el-row>
              <el-col :span="16">
                <div>学校</div><div>：</div><div style="font-size: 12px">西安科技大学</div>
                 <!-- <div>学校</div><div>：</div><div style="font-size: 12px">陕西科技大学</div> -->
              </el-col>
              <el-col :span="8">
                <div>校(院)长</div><div>：</div><div></div>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="16">
                <div>证书编号</div><div>：</div><div>10254687223332</div>
              </el-col>
              <el-col :span="8">
                <div style="width: auto">2019年9月23日</div>
              </el-col>
            </el-row>
            <!-- <img src="@/assets/img/seal.png" alt="" class="seal"> -->
             <img src="@/assets/img/seal1.png" alt="" class="seal">
          </div>
        </div>
      </div>
      <div class="download" v-if="isComplete" @click="download">
        <svgIcon size="20" icon-class="iconxiazaizhengshu"></svgIcon>&nbsp;
        <span>下载证书</span>
      </div>
      <div v-if="!isComplete" class="text">完成全部课程学习后，可获得学生能力微文凭</div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isComplete: false,
      info: {},
      loading: true
    }
  },
  created() {
    this.getInfo()
  },
  methods: {
    getInfo() {
      this.loading = true
      this.$get('basic/achieve/diplomaReport').then(res => {
        if(res.status === 1) {
          this.isComplete = res.data.finish
          res.data.claims.sort((a, b) => {
            let value1 = a.claimcode
            let value2 = b.claimcode
            return value1 - value2;
          })
          this.info = res.data
          this.loading = false
        } else {
          this.notify({
            title: '提示',
            message: res.message,
            type: 'error',
            duration: '2000'
          });
          this.loading = false
        }
      }).catch(err => {
        this.loading = false
        console.log(err)
      })
    },
    download() {
      location.href = `${process.env.BASE_URL}/basic/achieve/downLoad/diplomaReport?x-token=${localStorage.getItem('x_token')}`
    }
  }
}
</script>
<style lang="scss" scoped>
.certificate {
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
  min-height: calc(100vh - 120px);
  padding-bottom: 20px;
  box-sizing: border-box;
  div {
    box-sizing: border-box
  }
  .mainContainer {
    font-family:KaiTi;
    width: 100%;
    min-height: calc(100vh - 190px);
    background-color: #03b187;
    background-image: url('../../assets/img/bg1.png');
    background-size: 100% 100%;
    border-radius: 10px;
    padding: 52px 0 52px 207px;
    position: relative;
    .certificateContainer {
      // width: 560px;
      // min-height: calc(100vh - 294px);
      // background: #eee;
      // padding: 10px;
      .certificateBody {
        width: 560px;
        height: 100%;
        min-height: calc(100vh - 294px);
        overflow: hidden;
        padding: 42px 40px 35px 58px;
        text-align: center;
        background-image:url('../../assets/img/bg2.png');
        background-size: 100% 100%;
        background-color: #fff;
        position: relative;
        .bgImg {
          width: 440px;
          height: 430px;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          background-image: url('../../assets/img/bg3.png');
          background-size: 100% 100%;
          z-index: 0;
        }
        .logo {
          width:143px;
          height:25px;
          margin-bottom: 21px;
        }
        .title {
          h1 {
            font-size:23px;
            letter-spacing: 0.5em;
            font-weight: 700;
          }
          h3 {
            font-size:12px;
            letter-spacing: 0.2em;
            font-weight: 300;
            font-family:Arial;
          }
        }
        .content {
          font-size:10px;
          width: 100%;
          height: 100px;
          padding-top: 34px;
          padding-right: 85px;
          text-align: left;
          font-weight: 700;
          line-height: 1.3;
          position: relative;
          letter-spacing: 0.1em;
          span {
            font-weight: 500;
            padding: 0 5px;
          }
          img {
            width:66px;
            height:93px;
            position: absolute;
            top: 0;
            right: 0;
          }
        }
        .tableBox {
          padding: 0 20px 0 8px;
          min-height: 410px;
          table {
            color: #323232;
            margin-bottom: 5px;
            &:first-of-type {
              thead {
                th {
                  visibility: visible!important;
                }
              }
            }
            thead {
              th {
                font-size: 10px;
                text-align: left;
                font-weight: 700;
                &:nth-of-type(3),
                &:nth-of-type(4),
                &:nth-of-type(5) {
                  visibility: hidden;
                }
              }
            }
            tbody {
              text-align-last: left;
              tr {
                td {
                  font-size: 8px;
                  &:first-of-type {
                    text-align: left;
                    display: table;
                    letter-spacing: 0.1em;
                    div {
                      display: table-cell;
                      padding: 0 10px;
                    }
                    span {
                      display: table-cell;
                    }
                  }
                  &:nth-of-type(3) {
                    letter-spacing: 0.1em;
                  }
                  &:nth-of-type(4) {
                    letter-spacing: 0.1em;
                  }
                  &:nth-of-type(5) {
                    letter-spacing: 0.1em;
                  }
                }
              }
            } 
          }
        }
        .footer {
          margin-top: 35px;
          text-align: left;
          font-weight: 700;
          font-size: 10px;
          position: relative;
          .el-row {
            margin-bottom: 20px;
            .el-col {
              line-height: 12px;
              div {
                float: left;
                text-align: left;
                &:first-of-type {
                  width: 45px;
                  text-align:justify;
                  text-align-last: justify;
                }
                &:last-of-type {
                  letter-spacing: 0.1em;
                }
              }
            }
          }
          .seal {
            position: absolute;
            top: -35px;
            left: 120px;
            width: 80px;
          }
        }
      }
    }
    .download {
      width:270px;
      height:50px;
      line-height: 50px;
      border-radius: 25px;
      background-color: #fff;
      position: absolute;
      bottom: 110px;
      left: 820px;
      box-shadow:0px 0px 20px 0px rgba(3,177,135,0.8);
      cursor: pointer;
      text-align: center;
      &:active {
        background-color: rgba(255, 255, 255, 0.9);
      }
      span {
        letter-spacing: 0.2em;
        color: #03B187;
        font-family: '微软雅黑';
        font-size: 14px;
        font-weight: 700;
      }
    }
    .text {
      font-size:30px;
      font-family: '微软雅黑';
      letter-spacing: 0.1em;
      color: #fff;
      margin-top: 237px;
    }
  }
}
</style>


